@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin fixed($t, $l, $b, $r) {
    position: fixed;
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }    
}
@mixin placeholder {
    &::-webkit-input-placeholder {@content}
    &:-moz-placeholder           {@content}
    &::-moz-placeholder          {@content}
    &:-ms-input-placeholder      {@content}
}
@mixin border-inline-start($width, $style, $color) {
    border-inline-start-width: $width;
    border-inline-start-style: $style;
    border-inline-start-color: $color;
}  
@mixin border-inline-end($width, $style, $color) {
    border-inline-end-width: $width;
    border-inline-end-style: $style;
    border-inline-end-color: $color;
}
@mixin padding-start($value) {
    -webkit-padding-start: $value; 
        -moz-padding-start: $value;
            padding-inline-start: $value;
}
@mixin padding-end($value) {
    -webkit-padding-end: $value; 
        -moz-padding-end: $value;
            padding-inline-end: $value;
}
@mixin margin-start($value) {
    -webkit-margin-start: $value;
        -moz-margin-start: $value;
            margin-inline-start: $value;
}
@mixin margin-end($value) {
    -webkit-margin-end: $value;
        -moz-margin-end: $value;
            margin-inline-end: $value;
}
@mixin list-style-reset {
    list-style: none;
}
@mixin point-event {
    pointer-events: none;     
}
@mixin point-cursor {
    cursor: pointer;
}
@mixin visibility-hidden {
    visibility: hidden;   
    opacity: 0;
}
@mixin visibility-visible {
    visibility: visible;
    opacity: 1;
} 
@mixin btn-control {
    position: absolute;
    top: 50%;            
    display: flex;
    align-items: center;
    justify-content: center;  
    text-align: center;      
    @include point-cursor;
    transform: translateY(-50%);
    svg path {
        fill: var(--main-color);
    } 
    &[disabled] {
        color: #F2F2F4;
        cursor: not-allowed; 
        svg path {
            fill: #F2F2F4;
        }
    }  
}
@mixin font-family-normal {
    font-family: var(--body-font-family);
}
@mixin transition {
    transition: all 0.35s ease-in-out 0s;
}
@mixin fw-custom {
    font-weight: 500;
} 
@mixin radius-circle { 
    border-radius: 50%; 
}
@mixin letter-spacing($number) {
    // letter-spacing: calc((#{$number}/ 1000) * 1em);
    letter-spacing: ($number / 1000) * 1em;
}
%webkit-scrollbar-track {
    box-shadow: inset 0 0 4px #d0d0d0;
    -webkit-box-shadow: inset 0 0 #d0d0d0;
    background-color: #d0d0d0;
    border-radius: 5px;
}
%webkit-scrollbar {
    width: 4px;
    background-color: #d0d0d0;
    border-radius: 5px;
}
%webkit-scrollbar-thumb {
    background-color: var(--main-color);
    border-radius: 5px;
}
