@mixin icon-loading {
    &:before {
        content: "";
        @include ab(0, 0, 0, 0);   
        margin: auto;
        background: var(--btn1-hover-cl);               
        @include svg-loading;  
    }
}
@keyframes fa-spin { 
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(359deg);
		transform: rotate(359deg);
	}
}
@keyframes mmfadeIn {
    from { 
        opacity: 0; 
    }
    to {
        opacity: 1;
    }
}   
@keyframes animation-top-bottom {  
    0% {
        opacity: 0;
        transform: translate(0, -30px); 
    } 
    100% { 
        opacity: 1;
        transform: translate(0, 0);  
    }
} 
@keyframes animation-bottom-top {  
    0% {
        opacity: 0; 
        transform: translateY(60px);  
    } 
    100% { 
        opacity: 1;   
        transform: translateY(0);   
    }
}    
@keyframes animation-right-left {
    from {
        opacity: 0;
        transform: translateX(465px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}   
@keyframes animation-loading1 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes animation-loading2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(14px, 0); 
    }
} 
@keyframes animation-show-loading2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(8px, 0); 
    }
} 
@keyframes animation-loading3 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}

// manage z-index
@function layer($name) {
    @if map-has-key($layer, $name) {
        @return map-get($layer, $name);  
    }
    @warn "The key #{$name} is not in the map ’$layer’";
    @return null;
};
$layer: ( 
    header-sticky: 9,
    dropdown-visible: 100,  
    dropdown-menu-mobile: 99, 
    popup-bg-black: 990, 
    popup-content: 995,  
    tooltip: 2,
    backtotop: 409,
    control-button: 1, 
    label-product: 1,
);